import { Component, ElementRef, Renderer, HostListener, Input, Output, Compiler , ViewContainerRef ,SecurityContext } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'vrplayer',
    template: `    
    <iframe width="560" height="315" [src]="safeResourceUrl" frameborder="0" allowfullscreen></iframe>
  `,
})
export class VrplayerDirective {
 @Input('options') options: any; 
 safeResourceUrl: any;
 html:any;
 constructor(private _sanitizer: DomSanitizer, private eleRef: ElementRef, private _renderer: Renderer, private compiler:Compiler,private viewContainerRef:ViewContainerRef ) { }
 ngOnInit(){ 
      console.log(this.eleRef);
      this.safeResourceUrl = this._sanitizer.bypassSecurityTrustResourceUrl(this.options.url);
   
    
     
     
    }

}



